@font-face {
    font-family: 'TATSanaSuksaBold';
    // font-style: normal;
    // font-weight: 100;
    src: url('../font/TATSanaChon-Bold.woff2') format('woff2');
}

@font-face {
    font-family: 'TATSanaSuksa';
    // font-style: normal;
    // font-weight: 100;
    src: url('../font/TATSanaSuksa-Regular.woff2') format('woff2');
}

@mixin default-font {
    // font-family: 'TATSanaSuksaBold';
    text-align: center;
    font-weight: bold;
    font-size: 16px;
    color: #333333;
    line-height: 1.8;
}

.banner {
    padding-top: 60px;

    .cover-img-1,
    .cover-img-2,
    .cover-img-3 {
        height: 350px;
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
    }

    .cover-img-2 {
        // @include img-banner;
        .logo-box {
            padding: 20px;
            width: 280px;
            margin: auto;
            img {
                vertical-align: middle;
                border-style: none;
                width: 100%;
                height: 100%;
            }
        }
    }
}

.cover-img-1 {
    background: url(../img/banner-left.png) no-repeat;
}

.cover-img-2 {
    background: url(../img/banner-center.png) no-repeat;
}

.cover-img-3 {
    background: url(../img/banner-right.png) no-repeat;
}

.reg-box {
    margin: 26px 0px;

    @include default-font;
    font-family: 'TATSanaSuksaBold';

    .range-time {
        margin-top: -10px;
        font-size: 48px;
        color: #e6332a;
    }

    a {
        color: #007bff;
        text-decoration: none;
        background-color: transparent;
    }

    .reg-btn {
        font-family: TATSanaSuksaBold;
        color: #fff;
        border-radius: 5px;
        box-shadow: 0 15px 30px 0 rgba(28, 78, 132, 0.4);
        background-color: #213a8f;
        font-size: 24px;
        margin: 10px 0px;
        width: 800px;
        padding: 10px;
    }

    .reg-btn:hover {
        background-color: #fbbc33;
        box-shadow: none;
    }

    hr {
        margin-top: -3%;
    }
}

.detail-box {
    margin: 70px 0px;

    @include default-font;
    font-family: 'TATSanaSuksa';
    text-align: left;

    .condition {
        z-index: 100;
    }

    .head-title {
        font-family: TATSanaSuksaBold;
        font-size: 36px;
        color: #e6332a;
    }

    .detail {
        font-family: TATSanaSuksa;
        font-weight: 400;
        font-size: 18px;
    }

    .title {
        font-family: TATSanaSuksaBold;
        font-weight: bold;
        font-size: 18px;
    }
}

span.nowrap {
    white-space: nowrap;
}

.tat-box {
    margin: 150px 0px;
    img {
        width: 100%;
        height: 100%;
    }
}

.logo-box {
    margin: 100px 0px;
    img {
        width: 102px;
    }
}

