@media only screen and (max-width: 769px) {
    .banner {
        .cover-img-1,
        .cover-img-2,
        .cover-img-3 {
            height: 242px;
        }
        .cover-img-2 {
            .logo-box {
                width: 146px;
                height: 173px;
            }
        }
    }

    .reg-box {
        .range-time {
            font-size: 36px;
        }

        .reg-btn {
            font-size: 18px;
        }

        hr {
            margin-top: -4.5%;
        }
    }

    .detail-box {
        .head-title {
            font-size: 24px;
            line-height: 1.3;
        }

        .detail {
            font-size: 14px;
        }

        .title {
            font-size: 14px;
        }
    }

    // .detail-box,
    // .tat-box,
    // .logo-box {
    .container {
        padding-right: 30px;
        padding-left: 30px;
    }
    // }
}

@media only screen and (max-width: 576px) {
    .banner {
        .cover-img-1,
        .cover-img-2,
        .cover-img-3 {
            height: 221px;
        }

        .cover-img-2 {
            .logo-box {
                width: 110px;
                height: 131px;
                padding: 0;
            }
        }
    }
    .cover-img-1 {
        background: url(../img/banner-left-s.png) no-repeat;
    }

    .cover-img-2 {
        background: url(../img/banner-center-s.png) no-repeat;
    }

    .cover-img-3 {
        background: url(../img//banner-right-s.png) no-repeat;
    }

    .reg-box {
        .range-time {
            font-size: 30px;
        }

        .reg-btn {
            font-size: 18px;
            width: 100%;
        }
    }

    // .detail-container {
    //     padding-right: 30px;
    //     padding-left: 30px;
    // }

    .detail-box {
        .head-title {
            font-size: 24px;
            line-height: 1.6;
        }

        .detail {
            font-size: 16px;
        }

        .title {
            font-size: 16px;
        }
    }

    .tat-box {
        margin: 80px 0px;
        text-align: center;

        img {
            width: 80%;
        }
    }

    .logo-box {
        margin: 80px 0px;

        img {
            width: 56px;
        }
    }
}
